import { useForm, Form } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import InputMask from "react-input-mask";
import * as yup from "yup";
import {
  Card,
  CardBody,
  Input,
  Button,
  Typography,
} from "@material-tailwind/react";
import "./App.css";
import { InputHelperText } from "./components/InputHelperText";
import { InputWithHelperText } from "./components/InputWithHelperText";

const schema = yup
  .object({
    firstName: yup.string().required(" ").min(2),
    lastName: yup.string().required(" ").min(2),
    middleName: yup.string(),
    email: yup
      .string()
      .required(" ")
      .email(",   e-mail "),
    age: yup
      .number(",   ")
      .positive(",   ")
      .integer(",   ")
      .required(" "),
    childAge: yup
      .number(",   ")
      .positive(",   ")
      .integer(",   ")
      .required(" "),
    phone: yup
      .string()
      .required(" ")
      .length(18, ",    "),
  })
  .required();

function App() {
  return (
    <div className="App">
      <SimpleRegistrationForm />
    </div>
  );
}

export default App;

export function SimpleRegistrationForm() {
  const {
    register,
    formState: { errors },
    control,
  } = useForm({
    resolver: yupResolver(schema),
  });

  // console.log(register("lastName", { required: true }));
  // 1-
  // 2-
  // 3-
  // 4- 
  // 5- 
  // 6-E-mail
  // 7-   /
  return (
    <Card color="transparent" shadow={false} className="App-card">
      <CardBody>
        <Typography variant="h5" color="blue-gray">
          ,  
        </Typography>
        {/* <Typography color="gray" className="mt-1 font-normal">
        Enter your details to register.
      </Typography> */}
        <Form
          encType="application/json"
          action="http://158.160.82.110:3000/save-data"
          onSuccess={() => {
            // alert("Your application is updated.");
          }}
          onError={() => {
            alert("-   ...");
          }}
          control={control}
          className="mt-8 pl-5 pr-5 mb-8"
          style={{ width: "100%" }}
        >
          <div className="mb-4 flex flex-col gap-6">
            <InputWithHelperText
              label={""}
              register={register("lastName")}
              errorMsg={errors.lastName?.message}
            />
            <InputWithHelperText
              label={""}
              register={register("firstName")}
              errorMsg={errors.firstName?.message}
            />
            <InputWithHelperText
              label={" ( )"}
              register={register("middleName")}
              errorMsg={errors.middleName?.message}
            />
            <InputWithHelperText
              label={"Email"}
              register={register("email")}
              errorMsg={errors.email?.message}
            />

            <div>
              <InputMask
                mask="+7 (999) 999-99-99"
                maskChar={null}
                {...register("phone")}
              >
                {(inputProps) => (
                  <Input
                    {...inputProps}
                    error={errors.phone?.message}
                    type="tel"
                    size="lg"
                    label=" "
                  />
                )}
              </InputMask>
              {errors.phone?.message && (
                <InputHelperText errorMsg={errors.phone?.message} />
              )}
            </div>
            <InputWithHelperText
              label={" "}
              register={register("age")}
              errorMsg={errors.age?.message}
              extraErrorMsg={",   "}
            />
            <InputWithHelperText
              label={" "}
              register={register("childAge")}
              errorMsg={errors.childAge?.message}
              extraErrorMsg={",   "}
            />
          </div>
          <Button
            className="mt-6 hover:scale-[1.02] focus:scale-[1.02] active:scale-100"
            fullWidth
            type="submit"
          >
            
          </Button>
        </Form>
      </CardBody>
    </Card>
  );
}
